* {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
  font-family: "Inter", sans-serif;
  font-weight: 300;
  font-size: 13px;
  line-height: 22px;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
.container {
  width: 100%;
  max-width: 576px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}
@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media screen and (min-width: 1366px) {
  .container {
    max-width: 1200px;
    padding-left: 15px;
    padding-right: 15px;
  }
}
.vis-hidd {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}
a {
  text-decoration: none;
}
.big-title {
  font-size: 37px;
  font-family: "Bellota Text", cursive;
  line-height: 49px;
  margin-top: 0;
  margin-bottom: 15px;
}
.form__fieldset {
  padding: 0;
  margin: 0;
  border: none;
}
.form__text-input {
  background-color: #f9f7f7;
  border: none;
  border-radius: 3px;
  padding-top: 8px;
  padding-bottom: 9px;
  padding-left: 10px;
  padding-right: 10px;
}
.form__text-input::placeholder {
  color: #cac8c3;
}
.btn {
  font-weight: bold;
  font-size: 12px;
  line-height: 24px;
  display: inline-block;
  padding-top: 3px;
  padding-bottom: 3px;
  padding-left: 15px;
  padding-right: 15px;
  text-align: center;
  border-radius: 3px;
}
.btn--brown {
  border: 1px solid #c1bd98;
  background-color: #c1bd98;
  color: #ffffff;
}
.btn--brown:hover {
  background-color: #d6d09b;
  border: 1px solid #d6d09b;
}
.btn--brown:active {
  background-color: #9a9995;
  border: 1px solid #9a9995;
}
.btn--transparent {
  border: 1px solid #c1bd98;
  color: #c1bd98;
  background-color: transparent;
}
.btn--transparent:hover {
  background-color: #f9f7f7;
  border: 1px solid #f9f7f7;
}
.btn--transparent:active {
  background-color: #9a9995;
  border: 1px solid #9a9995;
}
.select2-container--default {
  border: none;
  margin-bottom: 10px;
  height: 32px;
}
.select2-container--default .selection {
  height: 32px;
}
.select2-container--default .select2-selection--single {
  background-color: #f9f7f7;
  border: none;
  color: #76746e;
  height: 32px;
}
.select2-container--default .select2-selection--single .select2-selection__rendered {
  color: #76746e;
  height: 32px;
  padding-top: 2px;
}
.select2-container--default .select2-selection--single .select2-selection__arrow {
  top: 3px;
}
.select2-container--default .select2-selection--single .select2-selection__arrow b {
  border: none;
  width: 11px;
  height: 7px;
  margin-left: -10px;
  background-image: url("../img/dropdown-arrow.png");
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
}
.select2-container--open .select2-selection--single .select2-selection__arrow b {
  border: none;
  width: 11px;
  height: 7px;
  margin-left: -10px;
  background-image: url("../img/dropdown-arrow.png");
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
  transform: rotate(180deg);
}
.select2-results__option {
  background-color: #f9f7f7;
  border-radius: 4px;
}
.select2-container--default .select2-results__option--highlighted.select2-results__option--selectable {
  background-color: #c1bd98;
}
.select2-dropdown {
  border: 1px solid #d9d8d4;
  border-radius: 4px;
  transition: all ease 0.3s;
}
.select2-dropdown .select2-dropdown--below {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}
.select2-container--default .select2-search--dropdown .select2-search__field {
  border-radius: 3px;
  border: 1px solid #c1bd98;
}
.header__wrapper {
  min-height: 50px;
  padding-top: 12px;
  position: relative;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
}
.header__logo {
  position: relative;
}
.header__logo::after {
  content: "";
  display: block;
  width: calc(100% + 30px);
  height: 1px;
  background-color: #d9d8d4;
  position: absolute;
  bottom: -12px;
  left: -15px;
}
.header__logo-img {
  height: 28px;
  object-fit: cover;
}
.header__navigation {
  display: none;
  order: 2;
  background-color: #fbf9f9;
  width: calc(100% + 30px);
  margin-left: -15px;
  padding-left: 15px;
  padding-right: 15px;
}
.header__navigation-list-item {
  font-weight: 300;
  font-size: 20px;
  line-height: 27px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.header__navigation-list-item .header__navigation-link {
  color: #76746e;
}
.header__navigation-list-item--active .header__navigation-link {
  color: #c1bd98;
}
.header__sub-list {
  display: none;
}
.header__logged-block {
  display: none;
}
.header__user {
  background-color: #fbf9f9;
  width: calc(100% + 30px);
  margin-left: -15px;
  padding-left: 15px;
  padding-right: 15px;
  margin-top: 12px;
}
.header__user-list {
  display: none;
  padding-top: 30px;
  padding-bottom: 30px;
  justify-content: space-between;
}
.header__user-list .header__user-list-item {
  width: 100%;
  max-width: 168px;
}
.header__user-list .header__user-link {
  width: 100%;
  max-width: 168px;
}
.header__logged-list {
  padding-bottom: 0px;
}
.header__user--logged .header__logged-block {
  display: block;
}
.header__user--logged .header__user-list {
  display: none!important;
}
.header__user--logged .header__logged-icon {
  position: absolute;
  top: 9px;
  right: 55px;
  animation: scale 0.3s;
}
.header__user--logged .header__logged-name {
  display: none;
  font-size: 13px;
  line-height: 20px;
  color: #76746e;
  padding-top: 20px;
  padding-bottom: 15px;
  border-bottom: 1px solid #d9d8d4;
}
.header__user--logged .header__logged-list {
  display: none;
}
.header__user--logged .header__logged-list-item {
  font-weight: 300;
  font-size: 20px;
  line-height: 27px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.header__mobile-btn {
  height: 29px;
  width: 30px;
  position: absolute;
  top: 12px;
  right: 15px;
  background-color: transparent;
  border: none;
  outline: none;
}
.header__mobile-btn--gamburger {
  animation: scale 0.3s;
  height: 29px;
  width: 30px;
}
.header__mobile-btn--gamburger::after {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: #c1bd98;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 1px;
}
.header__mobile-btn--gamburger::before {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: #c1bd98;
  position: absolute;
  bottom: 0;
  left: 0;
  border-radius: 1px;
}
.header__mobile-btn--gamburger .header__mobile-btn-decor {
  display: block;
  width: 80%;
  height: 3px;
  background-color: #c1bd98;
  position: absolute;
  top: 13px;
  right: 0;
  border-radius: 1px;
}
.header__mobile-btn--close {
  animation: scale2 0.4s;
}
.header__mobile-btn--close::after {
  content: "";
  display: block;
  width: 31px;
  height: 3px;
  background-color: #c1bd98;
  position: absolute;
  top: 13px;
  left: 0;
  transform: rotate(45deg);
  border-radius: 1px;
}
.header__mobile-btn--close::before {
  content: "";
  display: block;
  width: 31px;
  height: 3px;
  background-color: #c1bd98;
  position: absolute;
  bottom: 13px;
  left: 0;
  transform: rotate(135deg);
  border-radius: 1px;
}
.header__mobile-btn--close .header__mobile-btn-decor {
  display: none;
}
@keyframes scale {
  0% {
    transform: scale(0);
  }
  70% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes scale2 {
  0% {
    transform: scale(0);
  }
  70% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
.main-index {
  background-image: url("../img/map-background-mobile.png");
  background-size: 100% 762px;
  background-position: 0 0;
  background-repeat: no-repeat;
}
.present {
  padding-top: 30px;
}
.present__slogan {
  font-family: "Inter", sans-serif;
}
.present__form-wrapper {
  background-color: #ffffff;
  padding: 23px;
  border-radius: 4px;
}
.present__form-btn-close {
  display: none;
}
.present__form--active {
  padding-top: 20px;
  margin-top: -245px;
  background-color: #ffffff;
  position: relative;
  z-index: 2;
  width: calc(100% + 80px);
  margin-left: -40px;
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 20px;
}
.present__form--active .present__form-title {
  display: none;
}
.present__form--active .present__form-select,
.present__form--active .present__text-input {
  width: 100%;
  max-width: 100%;
  margin-bottom: 10px;
}
.present__form--active .present__fieldset--weight .present__form-wrapper-inner {
  display: flex;
  justify-content: space-between;
}
.present__form--active .present__fieldset--weight .present__form-wrapper-inner .present__text-input {
  width: 48%;
}
.present__form--active .present__form-btn-reset {
  position: absolute;
  top: 22px;
  right: 15px;
  font-size: 0;
  border: none;
  padding-right: 0;
}
.present__form--active .present__form-btn-reset::after {
  content: "Сбросить";
  font-size: 12px;
  line-height: 20px;
}
.present__form--active .present__form-btn-close {
  display: block;
  font-size: 12px;
  font-weight: 700;
  line-height: 24px;
  color: #c1bd98;
  position: absolute;
  top: 22px;
  left: 15px;
  border: none;
  background-color: transparent;
}
.present__form-mobile-title {
  text-align: center;
  display: block;
  font-size: 20px;
  font-family: "Bellota Text", cursive;
  line-height: 29px;
  margin-bottom: 20px;
}
.present__form-title {
  position: relative;
  display: block;
  font-size: 20px;
  font-family: "Bellota Text", cursive;
  line-height: 29px;
  padding-left: 30px;
}
.present__form-title::after {
  content: "";
  width: 23px;
  height: 35px;
  display: block;
  background-image: url("../img/present-form-title-icon.png");
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  left: 0;
}
.present__form-filter-mobile-btn {
  display: block;
  width: 100%;
  border-radius: 3px;
  border: 1px solid #c1bd98;
  font-weight: 700;
  margin-right: 20px;
  color: #c1bd98;
  padding: 9px 14px;
  margin-bottom: 20px;
  background-color: transparent;
  position: relative;
}
.present__form-filter-mobile-btn::after {
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  position: absolute;
  top: 8px;
  left: 6%;
  background-image: url("../img/filters-icon.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%;
}
.present__form-btn-submit {
  width: 100%;
  max-width: 100%;
}
